@import-normalize;
body {
  text-align: center; }

.container {
  width: 100vw;
  margin: 0 auto;
  text-align: center;
  align-items: center; }

header {
  height: 200px;
  width: 80vw;
  text-align: center;
  margin-left: auto;
  margin-right: auto; }
  header img {
    margin-left: auto;
    margin-right: auto;
    margin-top: 4vh;
    width: 80%; }

.giphy {
  width: 100vw;
  height: 200px;
  margin-top: 140px; }
  .giphy #search {
    display: flex;
    align-items: center;
    justify-content: center; }
  .giphy input {
    width: 60%;
    height: 6vw;
    border: 4px solid #903AF1;
    border-image: url("/public/images/search.gif") 20 round; }
  .giphy form {
    display: flex;
    height: 6vw;
    width: 100%; }
  .giphy button {
    height: 6.8vw;
    width: 6.8vw;
    border: none;
    background-image: url("/public/images/search.gif");
    background-color: #903AF1;
    background-size: cover; }

body {
  width: 100vw;
  background-color: white; }
